export interface TableDataItem {
  Method?: string;
  Model?: string;
  Scores: Record<string, any>;
}

export interface TableItem {
  name: string;
  data: TableDataItem[];
}

export interface RankData {
  [rankTypeKey: string]: {
    name: string;
    tables: {
      [tableKey: string]: TableItem;
    };
  };
}

